html {
  box-sizing: border-box;
  height: 100%;
  scrollbar-gutter: stable;
  scroll-behavior: smooth;
}

*,
::before,
::after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

:where(ul,ol):where([class]) {
  padding-left: 0;
}

body,
:where(blockquote,figure,fieldset):where([class]) {
  margin: 0;
}

:where(h1,h2,h3,h4,h5,h6,p,ul,ol,dl):where([class]) {
  margin-block: 0;
}

:where(dd[class]) {
  margin-left: 0;
}

:where(fieldset[class]) {
  padding: 0;
  border: none;
}

:where(ul[class]) {
  list-style: none;
}

:where(address[class]) {
  font-style: normal;
}

p {
  margin: 0;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

input,
textarea,
select,
button {
  font: inherit;
}

body {
  min-height: 100%;
  line-height: 1.5;
}

button,
label {
  cursor: pointer;
}

svg * {
  transition-property: fill,stroke;
}

:where(table) {
  border-collapse: collapse;
  border-color: currentColor;
}

@font-face {
  font-family: Kodchasan;
  font-display: swap;
  src: url("./assets/fonts/Kodchasan-Bold.woff2") format(woff2),url("./assets/fonts/Kodchasan-Bold.woff") format(woff),url("./assets/fonts/Kodchasan-Bold.ttf") format(ttf);
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: Kodchasan;
  font-display: swap;
  src: url("./assets/fonts/Kodchasan-Medium.woff2") format(woff2),url("./assets/fonts/Kodchasan-Medium.woff") format(woff),url("./assets/fonts/Kodchasan-Medium.ttf") format(ttf);
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: Kodchasan;
  font-display: swap;
  src: url("./assets/fonts/Kodchasan-SemiBold.woff2") format(woff2),url("./assets/fonts/Kodchasan-SemiBold.woff") format(woff),url("./assets/fonts/Kodchasan-SemiBold.ttf") format(ttf);
  font-weight: 600;
  font-style: normal;
}

html {
  font-size: 16px;
}

body {
  background-color: #fff;
  font-family: "Kodchasan","Open Sans",sans-serif;
  color: #000;
}

.body__fixed {
  overflow: hidden;
}

.container {
  max-width: 1146px;
  padding: 0 15px;
  margin: 0 auto;
}

.display--none {
  display: none;
}

.header {
  padding: 2em 2rem;
  position: relative;
}

.header__container {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  padding: 0;
  align-items: center;
}

.navigation {
  position: relative;
  transition: all .3s;
  z-index: 0;
}

.logo_wrapper {
  z-index: 1;
}

.logo_wrapper a {
  display: block;
}

.logo {
  max-width: 93px;
  max-height: 40px;
  display: block;
  fill: #ffa638;
  transition: all .3s;
}

.logo:hover,
.logo:focus-visible {
  fill: #ad5f00;
}

.nav-list {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
}

.nav_item a {
  display: block;
  font-size: .9rem;
  font-family: Kodchasan;
  font-weight: 500;
  padding: 0 min(20px,1.5vw);
  transition: all .3s;
}

.nav_item a:hover,
.nav_item a:focus-visible {
  color: #ad5f00;
}

.nav_item--active a {
  font-weight: 700;
  pointer-events: none;
}

.burger {
  position: relative;
  flex-basis: 25px;
  flex-shrink: 0;
  display: none;
  height: 18px;
  padding: 0;
  outline: none;
  border: none;
  background-color: unset;
  z-index: 1;
}

.burger__mid-line {
  display: block;
  width: 100%;
  height: 2px;
  background-color: #e58311;
  border-radius: 20px;
  transition: all .3s;
}

.burger__mid-line::before,
.burger__mid-line::after {
  content: "";
  position: absolute;
  border-radius: 20px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #e58311;
  transition: all .3s;
}

.burger__mid-line::before {
  top: 0;
}

.burger__mid-line::after {
  bottom: 0;
}

.hero {
  padding: 0 1rem;
}

.hero__container {
  position: relative;
  background-color: #e58311;
  background: url("../assets/img/hero_pic.png") center right/686px 650px no-repeat,url("../assets/img/honey_text.png") center right/1408px 650px no-repeat,linear-gradient(111deg, #ff9d2d 0%, #ffd877 100%);
  max-width: 1500px;
  padding: 11.38rem 5rem 8.63rem;
  border-radius: 1.88rem;
}

.hero__wrapper {
  max-width: 1146px;
  margin: 0 auto;
  position: relative;
}

.hero__title {
  margin-bottom: 4.38rem;
  max-width: 6.58em;
  font-size: 6.25rem;
  font-weight: 700;
}

.hero__title span {
  display: block;
  white-space: nowrap;
  font-size: 4.25rem;
}

.hero__title span:nth-child(2) {
  text-indent: min(.98em,10vw);
}

.hero__title .hero__bee-wrapper {
  display: inline-block;
  height: 120px;
  min-width: 190px;
  width: min(275px,20vw);
  border-bottom: 2px solid rgba(0,0,0,.5);
  margin-right: 20px;
}

.hero__bee {
  display: inline-block;
  width: 189px;
  height: 120px;
  vertical-align: baseline;
}

.hero__button {
  display: block;
  font-size: 1.125rem;
  font-weight: 700;
  line-height: 1.6;
  padding: 1em 2.78em;
  color: #000;
  border: 2px solid #000;
  transition: all .3s;
}

.hero__button:hover,
.hero__button:focus-visible {
  color: #e58311;
  background-color: #000;
  outline: none;
}

.hero__button--black {
  background-color: #000;
  color: #fff;
}

.webp .hero__container {
  background: url("../assets/img/hero_pic.webp") center right/686px 650px no-repeat,url("../assets/img/honey_text.webp") center right/1408px 650px no-repeat,linear-gradient(111deg, #ff9d2d 0%, #ffd877 100%);
}

.avif .hero__container {
  background: url("../assets/img/hero_pic.avif") center right/686px 650px no-repeat,url("../assets/img/honey_text.avif") center right/1408px 650px no-repeat,linear-gradient(111deg, #ff9d2d 0%, #ffd877 100%);
}

.buttons-wrapper {
  display: flex;
  gap: min(1.875rem,3vw);
}

.honey-pouring-wrapper {
  position: absolute;
  top: 1rem;
  left: 0;
  translate: -50% 0;
}

.shops {
  padding-top: 8.125rem;
}

.shops__title {
  font-weight: 700;
  font-size: 2.8rem;
  line-height: 1.6;
  margin-bottom: 1.11em;
}

.shops__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 2rem;
}

.shop__luxopica {
  aspect-ratio: 9/2;
  width: 144px;
  height: auto;
}

.shop__miu-miu {
  aspect-ratio: 6/1;
  width: 228px;
  height: auto;
}

.shop__dunkin {
  aspect-ratio: 17/3;
  width: 204px;
  height: auto;
}

.shop__burberry {
  aspect-ratio: 53/7;
  width: 265px;
  height: auto;
}

.about {
  padding: 8.125rem 1rem 0;
}

.about__container {
  display: flex;
  flex-direction: column;
  gap: 6.25rem;
  max-width: 1407px;
  padding: 0;
}

.about-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 2rem;
  gap: 2rem;
}

.about-card:nth-child(even) {
  flex-direction: row-reverse;
  padding-left: 2rem;
}

.about-card_img-wrapper {
  position: relative;
  height: 784px;
  flex-basis: 827px;
  flex-grow: 1;
  background: url("../assets/img/honeycomb.png") center/contain no-repeat;
}

.about-card_img-wrapper img {
  position: absolute;
  top: 50%;
  left: 50%;
  translate: -50% -50%;
  object-fit: contain;
}

.about-card_text-wrapper {
  flex-basis: 516px;
}

.about-card__title {
  font-weight: 700;
  font-size: 2.8rem;
  line-height: 1.45;
  margin-bottom: .67em;
}

.about-card__descr {
  font-weight: 600;
  font-size: 1.375rem;
  line-height: 1.6;
  margin-bottom: 2.27em;
}

.about-card__buton {
  font-size: 1.125rem;
  color: #fff;
  font-weight: 700;
  line-height: 1.6;
  background-color: #e58311;
  padding: .83em 2.22em;
  border: none;
  transition: all .3s;
}

.about-card__buton:hover,
.about-card__buton:focus-visible {
  background-color: #ad5f00;
}

.products {
  padding: 6.25rem 1rem 0;
}

.products .container {
  padding: 0;
}

.products__title {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  font-size: 2.8rem;
  font-weight: 700;
  line-height: 1.6;
  margin-bottom: 1.11em;
}

.see-all {
  font-size: 1.125rem;
  color: #e58311;
  font-weight: 700;
  line-height: 1.6;
  transition: all .3s;
  text-decoration: underline;
}

.see-all:hover {
  color: #ad5f00;
}

.products__cards {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
  gap: 1.875rem;
}

.product-card {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(229,130,17,.07);
  padding: 1.75rem 0 1.75rem 1.875rem;
  gap: .5rem;
}

.product-card__img {
  flex-basis: 230px;
  flex-shrink: 1;
  text-align: center;
  height: 100%;
}

.product-card__img img {
  display: inline-block;
}

.product-card__info {
  flex-basis: 300px;
  flex-shrink: 2;
  overflow: hidden;
}

.product-card__title {
  font-size: 1.75rem;
  font-weight: 600;
  line-height: 1.4;
  margin-bottom: .71em;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.product-card__descr {
  font-size: 1rem;
  font-weight: 600;
  line-height: 1.6;
  margin-bottom: 1.875em;
}

.product-card__buton {
  font-size: 1.125rem;
  color: #e58311;
  font-weight: 700;
  line-height: 1.6;
  background-color: unset;
  border: 1.5px solid #e58211;
  padding: .625rem 1.875rem;
  transition: all .3s;
}

.product-card__buton:hover,
.product-card__buton:focus-visible {
  background-color: #e58311;
  color: #fff;
}

.footer {
  margin-top: 8.1rem;
  background: linear-gradient(111deg, #ff9d2d 0%, #ffd877 100%);
