}

.footer__socials {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2.5rem;
}

.footer__socials svg {
  object-fit: contain;
  width: 100%;
  height: 100%;
  transition: all .3s;
  fill: #000;
}

.footer__socials svg:hover,
.footer__socials svg:focus-visible {
  fill: #fff;
  outline: none;
}

.footer__fb {
  width: 10px;
  height: 20px;
}

.footer__tw {
  width: 24px;
  height: 20px;
}

.footer__google {
  width: 27px;
  height: 18px;
}

.footer__pinterest {
  width: 16px;
  height: 21px;
}

.footer__stores {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1.25rem;
}

.footer__google-play svg {
  width: 139px;
  height: 41px;
  transition: all .3s;
}

.footer__google-play svg:hover,
.footer__google-play svg:focus-visible {
  transform: scale(1.1);
  outline: none;
}

.footer__app-store svg {
  width: 132px;
  height: 42px;
  transition: all .3s;
}

.footer__app-store svg:hover,
.footer__app-store svg:focus-visible {
  transform: scale(1.1);
  outline: none;
}

