@media (max-width: 300px) {
  .footer__stores {
    flex-direction: column;
    gap: .5rem;
  }
}

@media (prefers-reduced-motion: reduce) {
  *,
  ::before,
  ::after {
    animation-duration: .01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: .01ms !important;
    scroll-behavior: auto !important;
  }
}

@media (hover: none) {
  .logo:hover,
  .logo:focus-visible {
    fill: #ffa638;
  }

  .nav_item a:hover,
  .nav_item a:focus-visible {
    color: #000;
  }

  .hero__button:hover,
  .hero__button:focus-visible {
    background-color: unset;
    color: #000;
  }

  .hero__button--black:hover {
    background-color: #000;
    color: #fff;
  }

  .about-card__buton:hover,
  .about-card__buton:focus-visible {
    background-color: #e58311;
  }

  .see-all:hover {
    color: #e58311;
  }

  .product-card__buton:hover,
  .product-card__buton:focus-visible {
    color: #e58311;
    background-color: unset;
  }

  .footer__logo:hover,
  .footer__logo:focus-visible {
    fill: #000;
  }

  .footer__socials svg:hover,
  .footer__socials svg:focus-visible {
    fill: #000;
  }

  .footer__google-play svg:hover,
  .footer__google-play svg:focus-visible {
    transform: none;
  }

  .footer__app-store svg:hover,
  .footer__app-store svg:focus-visible {
    transform: none;
  }
}